
import Vue from 'vue'
const state = {
    hotSearch: [],     //热门搜索
    hotSearchFirst: "",//文本框搜索内容
}

const getters = {
    hotSearch( state ){
        return state.hotSearch;
    },
    hotSearchFirst( state ){
        // return state.hotSearch[state.hotSearch.length-1];
        return state.hotSearchFirst
    }
}

const actions = {
    getSearch( { commit } ){
        Vue.http.jsonp("https://list.mogujie.com/module/mget",{
            params:{
                code: "sketch,hotWord",
                platform: "H5"
            }
        }).then( ( { body : { data } } ) => {
            commit("getSearch",data);
        })
    },
}

const mutations = {
    getSearch( state, rs ){
        state.hotSearch = rs.hotWord.data;
        state.hotSearchFirst = rs.sketch.data.query;
        for (let index = 0; index < state.hotSearch.length; index++) {      //给无颜色的数据上色
            if(!state.hotSearch[index].color){
                state.hotSearch[index].color = "#666";
            }
        }
    }
}

export default {

	state,
	getters,
	actions,
	mutations

}


