<template>
  	<div>
		<search></search>
		<banner></banner>
		<penny></penny>
		<boon></boon>
		<hot-marker></hot-marker>
		<gress></gress>
  	</div>
</template>

<script>
	import Search from "./../../Temp/search/search.vue"
	import Banner from "./banner/banner.vue"
	import Penny from "./penny/penny.vue"
	import Gress from "./gress/gress.vue"
	import Boon from "./boon/boon.vue"
	import HotMarker from "./hotMarker/hotMarker.vue"
	export default {
		methods:{
            getTop(){
                document.documentElement.scrollTop = 0;             //跳转该路由时让滚动条置顶
            }
        },
		mounted(){
			this.$store.dispatch('getDatas');
			this.$store.dispatch('getGress');
			// this.$store.dispatch('getSearch');
            this.getTop();                                      //加载页面时调用置顶函数
		},
		deactivated(){
            this.$destroy();    //手动调用destroy钩子
        },
		components:{
			Search,
			Banner,
			HotMarker,
			Penny,
			Boon,
			Gress
		}
	};
</script>

<style lang="less" scoped>
	
</style>


