<template>
  	<div>
		<swiper :options="swiperOption" ref="mySwiper" class="swiper">
                <swiper-slide v-for="(item,index) in banner" :key='index'>
                    <img :src="item.image_800">
                </swiper-slide>
				<!-- 分页标签 -->
				<div class="swiper-pagination" slot="pagination"></div>
        </swiper>
  	</div>
</template>

<script>
	import { mapGetters, mapActions, mapMutations } from "vuex"
	export default {
		computed:{
			...mapGetters([
				"banner"
			])
		},
		data() {
            return {
				swiperOption: { //swiper 原生配置
					autoplay: 5000,
					loop : true,
					//分页标签配置
					pagination : '.swiper-pagination',
				}
			}
		}
	};
</script>

<style lang="less" scoped>
	.swiper{
        background: white;
        height: 2.5rem;
        img{
            width: 100%;
            height: 100%;
        }
        .swiper-pagination{
            position: relative;
            top: -1rem;
            left: -2.5rem;
            color: red;
        }
    }
</style>


