<template>
  	<div class="container">
        <p class="title">猜你喜欢</p>
		<div class="boxContainer">
			<div class="boxItem" v-for="(item,index) in gress" :key='index'>
				<div class="imgContainer">
					<img :src="item.show.img">
				</div>
				<div class="textContent">
					<div class="tips">
						<span class="tipsDetails" v-for="(i,index) in item.props" :key='index'>
							&nbsp;{{ i }}&nbsp;
						</span>
					</div>
					<div class="pContainer">
						<p class="price">
							{{ item.price }}
						</p>
						<p class="starIcon"></p>
						<p class="count">
							{{ item.sale }}
						</p>
					</div>
				</div>
			</div>
		</div>
  	</div>
</template>

<script>
	import { mapGetters, mapActions, mapMutations } from "vuex"
	export default {
		computed:{
			...mapGetters([
				"gress"
			])
		}
	};
</script>

<style lang="less" scoped>
	.container{
		margin-top: 0.4rem;
		margin-bottom: 0.8rem;
		text-align: center;
		.title{
			background: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAATkAAAAKAgMAAACyFsHVAAAADFBMVEVMaXEzMzMzMzP///8xqFqyAAAAAnRSTlMAtlSR24EAAAABYktHRAMRDEzyAAAAQElEQVQoz2NgoDkQIVZhAHHKMok0jnEJccpWORBnHttK4pStmkCceVJYLV6FCkg1TwtVewPt/Uvt+KB6eqEIAACpih6NjokEAgAAAABJRU5ErkJggg==") no-repeat center center / 45% 35%;
			font-size: 0.25rem;
			margin-bottom: 0.2rem;
		}
		.boxContainer{
			overflow: hidden;
			.boxItem{
				height: 5.4rem;
				width: 46.5%;
				margin-left: 0.15rem;
				margin-bottom: 0.15rem;
				background: white;
				border-radius: 0.06rem;
				float: left;
				.imgContainer{
					height: 73%;
					img{
						width: 100%;
						height: 100%;
					}
				}
				.textContent{
					height: 27%;
					.tips{
						margin: 0 0.1rem;
						height: 60%;
						text-align: center;
						overflow: hidden;
						span{
							line-height: 0.3rem;
							margin-top: 0.09rem;
							float: left;
							height: 0.3rem;
							font-size: 0.18rem;
							background: rgb(239, 243, 246);
							margin-left: 0.05rem;
							color: #666;
						}
					}
					.pContainer{
						height: 40%;
						margin: 0.15rem 0.08rem;
						p{
							font-size: 0.3rem;
							line-height: 0.3rem;
						}
						.price{
							font-weight: bold;
							float: left;
						}
						.count{
							float: right;
							color: #aaa;
							font-size: 0.2rem;
						}
						.starIcon{
							float: right;
							width: 0.3rem;
							height: 0.3rem;
							background: url("data:image/png;base64,") center right no-repeat / 70% 70%;
						}
					}
				}
			}
		}
	}
</style>


