  	</div>
</template>

<script>
	import { mapGetters, mapActions, mapMutations } from "vuex"
	export default {
		computed:{
			...mapGetters([
				"hotMarker"
			])
		}
	};
</script>

<style lang="less" scoped>
	.container{
        margin-top: 0.2rem;
        background-color: white;
        padding-top: 0.01rem;
        .bg{
            text-align: center;
            margin-top: 0.25rem;
            .title{
                background: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAATkAAAAKAgMAAACyFsHVAAAADFBMVEVMaXEzMzMzMzP///8xqFqyAAAAAnRSTlMAtlSR24EAAAABYktHRAMRDEzyAAAAQElEQVQoz2NgoDkQIVZhAHHKMok0jnEJccpWORBnHttK4pStmkCceVJYLV6FCkg1TwtVewPt/Uvt+KB6eqEIAACpih6NjokEAgAAAABJRU5ErkJggg==") no-repeat center center / 45% 35%;
                font-size: 0.25rem;
                margin-bottom: 0.2rem;
            }
            .hotMarkerDetail{
                display: flex;
                justify-content: space-around;
                flex-wrap: wrap;
            }
            .hotMarkerItem{
                width: 25%;
                img{
                    width: 1rem;
                    height: 1rem;
                }
                p{
                    font-size: 0.22rem;
                    margin-top: -0.1rem;
                    margin-bottom: 0.2rem;
                }
            }
        }
        

    }
</style>


