<template>
  	<div class="container">
		<div class="search">
			<search></search>
		</div>
		<div class="content">
			<left-scroll></left-scroll>
			<right-content></right-content>
		</div>
  	</div>
</template>

<script>
	import Search from "./../../Temp/search/search.vue"
	import LeftScroll from "./leftScroll/leftScroll.vue"
	import RightContent from "./rightContent/rightContent.vue"
	export default {
		mounted(){
			this.$store.dispatch('getLeftScroll');
			this.getTop();             								//加载页面时调用置顶函数
		},
		methods:{
            getTop(){
                document.documentElement.scrollTop = 0;             //跳转该路由时让滚动条置顶
            }
        },
		deactivated(){
            this.$destroy();    //手动调用destroy钩子
        },
		components:{
			Search,
			LeftScroll,
			RightContent
		}
	};
</script>

<style lang="less" scoped>
	.container{
		background-color: white;
		overflow: hidden;
		.search{
			width: 100%;
			position: fixed;
			z-index: 9999;
		}
		.content{
			margin-top: 0.82rem;
		}
	}
</style>


