<template>
  	<div>
		<ul class="nav">
            <li class="iconli">
                <i class="iconfont icon-xiaoxi"></i>
            </li>
            <li>
                <router-link to="/SearchPage">
                    <p class="searchP">
                        <i class="iconfont icon-sousuo"></i>
                        <span class="searchText">{{hotSearchFirst}}</span>
                    </p>
                </router-link>
            </li>
            <li class="iconli">
                <i class="iconfont icon-gouwuche"></i>
            </li>
        </ul>
  	</div>
</template>

<script>
	import { mapGetters, mapActions, mapMutations } from "vuex"
	export default {

		computed:{
			...mapGetters([
                "hotSearch",
                "hotSearchFirst"
			])
        },
	};
</script>

<style lang="less" scoped>
    .nav{
        background: white;
        top: 0;
        height: 0.8rem;
        border-bottom: 1px solid #ddd;
        width: 100%;
        text-align: center;
        overflow: hidden;
        li{
            width: 70%;
            line-height: 0.8rem;
            height: 100%;
            list-style: none;
            float: left;
            font-size: 0.3rem;
            .iconfont{
                font-size:0.35rem;
            }
            .searchP{
                background: rgb(238, 238, 238);
                border-radius: 0.06rem;
                height: 0.52rem;
                margin: 0.14rem 0;
                color: #999;
                .iconfont{
                    float: left;
                    top: 0.1rem;
                    margin: 0 0.1rem;
                    font-size:0.3rem;
                    line-height: 0.5rem;
                }
                .searchText{
                    font-size: 0.22rem;
                    float: left;
                    line-height: 0.5rem;
                }
            }
        }
        .iconli{
            width: 15%;
            color: #666;
        }
    }
</style>


