<template>
  	<div class="container">
		<div class="imgConainer" @click='$router.history.go(-1)'>
            <img :src="item.img">
        </div>
        <div class="content">
            <div class="title">
                <p>
                    {{ item.title }}
                </p>
            </div>
            <div class="price">
                <span class="symbol">￥</span>
                <span class="newPrice">{{ item.price }}</span>
                <span class="oldPrice">￥{{ item.orgPrice }}</span>
                <p class="free">免邮费</p>
            </div>
            <div class="choose">
                <p class="size">请选择：颜色</p>
                <p class="gt">&gt;</p>
            </div>
            <div class="commitContainer">
                <div class="commitList" v-for="(item,index) in commit" :key="index">
                    <div class="commitListBox">
                        <div class="photo">
                            <img :src="item.user.avatar">
                        </div>
                        <div class="commitListTitle">
                            <p class="uname">{{ item.user.uname }}</p>
                            <p class="commitListContent">{{ item.content }}</p>
                            <p class="date">{{ item.time }} {{ item.style }}</p>

                            <div v-if="item.images.length">
                                <div class="commitImgDivContainer">
                                    <div class="commitImgDiv" v-for="(v,i) in item.images" :key="i">
                                        <img :src="v">
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <footer>
            <div class="collect">
                <i class="iconfont icon-star"></i>
                <p>收藏</p>
            </div>
            <div class="shop">
                <i class="iconfont icon-fenlei"></i>
                <p>小店</p>
            </div>
            <div class="cart" @click="innerCart(item)">
                <span>加入购物车</span>
            </div>
            <div class="buy">
                <span>立即购买</span>
            </div>
        </footer>
  	</div>
</template>

<script>
	import { mapGetters, mapActions, mapMutations } from "vuex"
	export default {
        data(){
            return {
                item: {}
            }
        },
		computed:{
			...mapGetters([
                "commit",
			])
        },
        methods:{
            getTop(){
                document.documentElement.scrollTop = 0;             //跳转该路由时让滚动条置顶
            },
            innerCart(item){
                if(!item.count){
                    item.count = 1;
                }
                this.$store.dispatch('setCart',item);               //点击提交商品信息
            }
        },
        mounted(){
            this.item = this.$route.query.item;                     //获取router-link传递参数
            this.$store.dispatch('getCommit',this.item.iid);        //获取评论
            this.getTop();                                          //加载页面时调用置顶函数
        },
        deactivated(){
            this.$destroy();    //手动调用destroy钩子
        },
    };
</script>

<style lang="less" scoped>
    .container{
        color: #333;
        width: 100%;
        position: absolute;
        z-index: 9999;
        overflow: hidden;
        .imgConainer{
            background-color: white;
            height: 8.4rem;
            img{
                height: 100%;
                width: 100%;
            }
        }
        .content{
            width: 100%;
            background-color: #e9e9e9;
            margin-bottom: 0.85rem;
            .title{
                padding: 0.3rem 0.2rem 0.1rem;
                background-color: white;
                p{
                    font-size: 0.3rem;
                    word-wrap: break-word;
                }
            }
            .price{
                background-color: white;
                overflow: hidden;
                .symbol{
                    font-size: 0.2rem;
                    position: relative;
                    bottom: 0.55rem;
                    left: 0.15rem;
                }
                .newPrice{
                    font-size: 0.7rem;
                    position: relative;
                    bottom: 0.25rem;
                    left: -0.22rem;
                }
                .oldPrice{
                    font-size: 0.2rem;
                    text-decoration: line-through;
                    color: #999;
                    position: relative;
                    bottom: 0.4rem;
                    margin-left: -0.5rem;
                }
                .free{
                    font-size: 0.2rem;
                    color: #999;
                    position: relative;
                    top: -0.3rem;
                    left: 0.2rem;
                }
            }
            .choose{
                margin-top: 0.18rem;
                font-size: 0.22rem;
                color: #777;
                overflow: hidden;
                background-color: white;
                height: 0.8rem;
                line-height: 0.8rem;
                .size{
                    float: left;
                    margin-left: 0.2rem;
                }
                .gt{
                    float: right;
                    margin-right: 0.3rem;
                }
            }
            .commitContainer{
                background-color: white;
                margin-top: 0.18rem;
                font-size: 0.23rem;
                .commitList{
                    border-bottom: 0.01rem solid #eee;
                    .commitListBox{
                        padding: 0.3rem;
                        overflow: hidden;
                        .photo{
                            width: 1rem;
                            height: 1rem;
                            float: left;
                            img{
                                width: 100%;
                                height: 100%;
                            }
                        }
                        .commitListTitle{
                            line-height: 0.38rem;
                            float: left;
                            margin-left: 0.1rem;
                            width: 4.7rem;
                            .uname{
                                font-weight: bold;
                            }
                            .commitListContent{
                                font-size: 0.2rem;
                                width: 100%;
                            }
                            .date{
                                font-size: 0.2rem;
                                color: #777
                            }
                            .commitImgDivContainer{
                                width: 100%;
                                overflow: hidden;
                                .commitImgDiv{
                                    float: left;
                                    margin-right: 0.05rem;
                                    background-color: #ccc;
                                    width: 1.5rem;
                                    height: 1.5rem;
                                    overflow: hidden;
                                    img{
                                        width: 110%;
                                        height: 120%;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }

        footer{
            background-color: #fff;
            height: 0.85rem;
            width: 100%;
            position: fixed;
            bottom: -0.01rem;
            border-top: 0.01rem solid #ddd;
            div{
                float: left;
                text-align: center;
                line-height: 300%;
                font-size: 0.25rem;
                box-sizing: border-box;
            }
            .collect,.shop{
                font-size: 0.15rem;
                height: 100%;
                width: 15%;
                .iconfont{
                    font-size: 0.35rem;
                }
                p{
                    margin-top: -0.25rem;
                }
            }
            .collect{
                border-right: 0.01rem solid #ddd;
            }
            .cart{
                background-color: rgb(254, 230, 232);
                height: 100%;
                width: 35%;
                line-height: 0.85rem;
                color: rgb(252, 84, 128);
            }
            .buy{
                background-color: rgb(252, 84, 128);
                height: 100%;
                width: 35%;
                line-height: 0.85rem;
                color: snow;
            }
        }
    }
</style>


