<template>
  	<div class="container">
		<h1>超实惠-促销直达</h1>
		<div class="boxContainer">
			<div v-for="(item,index) in boon" :key='index' :class="'box'+index" class="box">
				<div :class="'pContainer'+index" class="pContainer">
					<p class="title" :class="'title'+index">{{ item.title }}</p>
					<p class="viceTitle" :class="'viceTitle'+index">{{ item.viceTitle }}</p>
				</div>
				<img :src="item.image">
			</div>
		</div>
  	</div>
</template>

<script>
	import { mapGetters, mapActions, mapMutations } from "vuex"
	export default {
		computed:{
			...mapGetters([
				"boon"
			])
		}
	};
</script>

<style lang="less" scoped>
.container{
	width: 100%;
	height: 5.6rem;
	background: white;
	margin-top: 0.2rem;
	h1{
		font-size: 0.23rem;
		height: 0.6rem;
		line-height: 0.7rem;
		padding-left: 0.2rem;
		font-weight: normal;
	}
	.boxContainer{
		border-top: 0.01rem solid #ddd;
		overflow: hidden;
		.box{
			width: 33.3%;
			box-sizing: border-box;
			float: left;
			text-align: center;
			.title{
				font-size: 0.23rem;
				font-weight: bold;
			}
			.viceTitle{
				font-size: 0.2rem;
				color: rgb(250,100,128);
				margin-top: 0.07rem;
			}
			img{
				width: 1.3rem;
			}
		}
		.box0{
			width: 50%;
			height: 2.688rem;
			border-bottom: 0.01rem solid #ddd;
			border-right: 0.01rem solid #ddd;
			img{
				width: 55%;
			}
			.title{
				margin-top: 0.37rem;
				font-weight: bold;
			}
		}
		.box1,.box2{
			width: 50%;
			height: 1.344rem;
			border-bottom: 0.01rem solid #ddd;
			overflow: hidden;
			.pContainer1,.pContainer2{
				float: left;
				width: 66%;
				padding: 0.2rem;
				padding-top: 0.38rem;
				.title{
					float: left;
					width: 50%;
				}
				.viceTitle{
					float: left;
				}
			}
			img{
				position: relative;
				top: -1.12rem;
				left: 1rem;
				width: 42%;
			}
		}
		.box3,.box4{
			border-right: 0.01rem solid #ddd;
		}
		.box3,.box4,.box5{
			height: 2.15rem;
			.pContainer{
				margin-top: 0.2rem;
			}
			img{
				padding-top: 0.1rem;
				width: 65%;
			}
		}
	}
}
	
</style>


