
import Vue from 'vue'
const state = {
    leftScroll: [],             //左侧导航栏
    rightContent: [],           //右侧详细内容
    rightTitle: [],             //右侧标题图
    rightContentBottom: [],     //右侧下方分类
    rightBottomDetails: [],     //右侧下方详情
    miniWallkey: "",            //右侧下方分类其中的参数
}

const getters = {
    leftScroll( state ){
        return state.leftScroll;
    },
    rightContent( state ){
        return state.rightContent;
    },
    rightTitle( state ){
        return state.rightTitle;
    },
    rightContentBottom( state ){
        return state.rightContentBottom;
    },
    rightBottomDetails( state ){
        return state.rightBottomDetails;
    },
    miniWallkey( state ){
        return state.miniWallkey;
    }
}

const actions = {
    getLeftScroll( { commit } ){
        Vue.http.jsonp("https://mce.mogucdn.com/jsonp/multiget/3",{
            params:{
                pids: "41789,12775"
            }
        }).then( ({data:{data}}) => {
            let datas = data[41789].list;
            commit("getLeftScroll",datas);
        })
    },
    getRightContent( { commit }, maitKey ){
        Vue.http.jsonp("https://mce.mogujie.com/jsonp/makeup/3",{
            params:{
                pid: maitKey
            }
        }).then( ( { body : { data } } ) => {
            commit("getRightContent",data);
        })
    },
    getRightContentBottom( { commit }, {miniWallkey, sortKey}){
        Vue.http.jsonp("https://list.mogujie.com/search",{
            params:{
                cKey : "h5-cube",
                fcid : miniWallkey,
                sort : sortKey
            }
        }).then( ( { body : { result } } ) => {
            commit("getRightContentBottom",result);
        })
    },
}

const mutations = {
    getLeftScroll( state, rs ){
        state.leftScroll = rs
    },
    getRightContent( state, rs ){
        state.rightContent = rs.categoryNavigation.list;
        if(!rs.bannerHead){
            state.rightTitle = "";
        }else{
            state.rightTitle = rs.bannerHead.list;
        }
    },
    getRightContentBottom( state, rs ){
        state.rightContentBottom = rs.sortFilter;
        // console.log(state.rightContentBottom);
        state.rightBottomDetails = rs.wall.docs;
        // console.log(state.rightBottomDetails);
        state.miniWallkey = rs.param.fcid;
    },
}

export default {

	state,
	getters,
	actions,
	mutations

}





