<template>
  	<div class="container">
		<a v-for="(item,index) in penny" :key='index' class="pennyItem">
            <img :src="item.image">
            <p>{{ item.title }}</p>
        </a>
  	</div>
</template>

<script>
	import { mapGetters, mapActions, mapMutations } from "vuex"
	export default {
		computed:{
			...mapGetters([
				"penny"
			])
        },
    }
</script>

<style lang="less" scoped>
    .container{
        overflow: hidden;
        background: white;
        padding-bottom: 0.2rem;
        .pennyItem{
            width: 20%;
            overflow: hidden;
            float: left;
            text-align: center;
            img{
                width: 0.9rem;
                height: 0.9rem;
                vertical-align: middle;
            }
            p{
                color: #606060;
                font-size: 0.22rem;
            }
        }
    }
</style>


