<template>
  	<div class="container">
		<ul class="nav">
            <li class="iconli" @click='$router.history.go(-1)'>
                <i class="iconfont icon-xiangzuojiantou"></i>
            </li>
            <li>
               <span>购物车</span>
            </li>
            <li class="iconli last">
				<span @click="clear()">清空</span>
                <i class="iconfont icon-xiaoxi"></i>
            </li>
        </ul>
		<div class="cartDetailsContainer">
			<div class="cartDetails" v-for="(v,i) in cart" :key="i">
				<div class="imgDiv">
					<img :src="v.img">
				</div>
				<div class="title">
					{{ v.title }}
				</div>
				<div class="price title">
					￥{{ v.price }}
				</div>
				<div class="orgPrice title">
					￥{{ v.orgPrice }}
				</div>
				<div class="count title">
					数量*{{v.count}}
				</div>
			</div>
		</div>
		<div class="content" v-if="!cart.length">
			<div class="iconDiv">
				<i class="iconfont icon-gouwuche"></i>
			</div>
			<div class="empty">
				<p>购物车还是空的哦~</p>
				<div class="box">
					<router-link to="/type">
						<p>去逛逛</p>
					</router-link>
				</div>
			</div>
		</div>
		<div class="clearCart">
			<div class="total">
				总金额：{{ total }}
			</div>
			<div class="buy">
				立即购买
			</div>
		</div>
  	</div>
</template>

<script>
import vue from 'vue'
	import { mapGetters, mapActions, mapMutations } from "vuex"
	export default {
		computed:{
			...mapGetters([
				"commit",
				"cart",
				"total"
			])
		},
		watch:{
			cart(curVal,oldVal){
　　　　　　　　　　console.log(curVal,oldVal);
　　　　　　　　},
		},
		methods:{
			clear(){
				this.$store.dispatch('clear');
			}
		}
	};


</script>

<style lang="less" scoped>
.container{
	width: 100%;
	height: 100%;
	overflow: hidden;
	.nav{
		position: fixed;
		top: 0;
        background: white;
        top: 0;
        height: 0.75rem;
        border-bottom: 1px solid #ccc;
        width: 100%;
        text-align: center;
		overflow: hidden;
		color: #555;
        li{
			line-height: 0.75rem;
			width: 70%;
            height: 100%;
            list-style: none;
            float: left;
            font-size: 0.3rem;
            .iconfont{
                font-size:0.35rem;
			}
		}
		.iconli{
			width: 15%;
			font-size: 0.2rem;
		}
		.last{
			span{
				position: relative;
				top: -0.05rem;
				right: 0.1rem;
			}
			position: absolute;
			right: 0.2rem;
			.iconfont{
				color: #aaa;
			}
		}
	}
	.cartDetailsContainer{
		margin-top: 0.8rem;
		margin-bottom: 0.9rem;
		font-size: 0.25rem;
		overflow: hidden;
		.cartDetails{
			background-color: white;
			width: 100%;
			margin-top: 0.2rem;
			border-top: 0.01rem solid #ddd;
			border-bottom: 0.01rem solid #ddd;
			padding: 0.3rem;
			overflow: hidden;
			.imgDiv{
				width: 1.5rem;
				overflow: hidden;
				height: 2rem;
				img{
					width: 100%;
					height: 100%;
				}
			}
			.title{
				line-height: 0.3rem;
				font-weight: 600;
				color: #666;
				width: 65%;
				font-size: 0.2rem;
				float: right;
				margin-top: -2rem;
				margin-right: 0.5rem;
			}
			.price{
				margin-top: -0.7rem;
				font-size: 0.3rem;
				color: #333;
			}
			.orgPrice{
				color: #aaa;
				margin-top: -0.45rem;
				text-decoration: line-through;
			}
			.count{
				color: #777;
				font-size: 0.2rem;
				margin-top: -0.2rem;
				margin-left: 0.5rem;
			}
		}
	}
	.content{
		margin-top: 0.8rem;
		text-align: center;
		font-size: 0.23rem;
		color: #777;
		.iconDiv{
			background-color:#cdcdcd;
			line-height: 2.2rem;
			width: 2.2rem;
			height: 2.2rem;
			border-radius: 2.2rem;
			margin: 3.2rem auto 0;
			.iconfont{
				font-size: 1rem;
				color: white;
				font-weight: 500;
			}
		}
		.empty{
			margin-top: 0.2rem;
			.box{
				margin-top: 0.2rem;
				p{
					margin: 0 auto;
					width: 1.7rem;
					height: 0.6rem;
					line-height: 0.6rem;
					font-size: 0.25rem;
					color: white;
					background-color: rgb(252, 89, 121);
					border-radius: 0.05rem;
				}
			}
		}
	}
	.clearCart{
		background-color: #fff;
		height: 0.85rem;
		width: 100%;
		position: fixed;
		bottom: 0rem;
		z-index: 9999;
		border-top: 0.01rem solid #ddd;
		div{
			float: left;
			text-align: center;
			line-height: 300%;
			font-size: 0.25rem;
			box-sizing: border-box;
		}
		.total{
			width: 50%; 
			background-color: rgb(254, 230, 232);
			height: 100%;
			line-height: 0.85rem;
			color: rgb(252, 84, 128);
		}
		.buy{
			width: 50%;
			background-color: rgb(252, 84, 128);
			height: 100%;
			line-height: 0.85rem;
			color: snow;
		}
	}
}
	
</style>


