<template>
  	<div class="container">
		<div v-if="flag" class="content">
			<router-link to="/cart">
				<div>
					<span>我的购物车</span>
				</div>
			</router-link>
			<div>
				<span>我的订单</span>
			</div>
			<div>
				<span>我的拼团</span>
			</div>
			<div>
				<span>我的钱包</span>
			</div>
			<div>
				<span>我的优惠券</span>
			</div>
			<div>
				<span>我收藏的商品</span>
			</div>
			<div>
				<span>我收藏的店铺</span>
			</div>
		</div>

		<div class="cancel" v-if="flag" @click="changeFlag">
			<span class="exit">退出登录</span>
		</div>
		<div class="sure" v-if="!flag" @click="changeFlag">
			<i class="iconfont icon-xiaoxi"></i>
			<span class="regest">微信登录</span>
		</div>
  	</div>
</template>

<script>
	export default {
		data(){
			return {
				flag: true,
			}
		},
		methods:{
			changeFlag(){
				this.flag = !this.flag;
			}
		}
	};
</script>

<style lang="less" scoped>
	.container{
		font-size: 0.3rem;
		width: 100%;
		// overflow: hidden;
		// text-align: center;
		.content{
			background-color: white;
			overflow: hidden;
			div{
				float: left;
				width: 100%; 
				height: 0.85rem;
				background-color: white;
				border-bottom: 0.01rem solid #ddd;
				line-height: 0.85rem;
				color: rgb(252, 84, 128);
				// margin-bottom: 0.2rem;
				span{
					margin-left: 0.8rem;
				}
			}
		}
		.cancel,.sure{
			margin-top: 1rem;
			text-align: center;
			width: 84%;
			margin-left: 8%;
			height: 0.7rem;
			line-height: 0.7rem;
			font-size: 0.25rem;
			font-weight: 100;
			border-radius: 0.07rem;
			color: white;
			background-color: rgb(252, 84, 128);;
			span{
				margin: 0 auto;
			}
			.regest{
				font-weight: 400;
			}
		}
		.sure{
			margin-top: 4rem;
			background-color: rgb(31, 185, 34);
			height: 0.85rem;
			line-height: 0.85rem;
			border-radius: 1rem;
			font-size: 0.32rem;
			.iconfont{
				font-weight: 800;
				font-size: 0.32rem;
			}
		}
	}
</style>


