
import Vue from 'vue'
const state = {
    banner: [],     //轮播图
    penny: [],      //一分夺宝
    hotMarker: [],  //热门市场
    boon: [],       //超实惠-促销直达
    gress: []       //猜你喜欢
}

const getters = {
	banner( state ){
        return state.banner;
    },
    penny( state ){
        return state.penny;
    },
    boon( state ){
        return state.boon;
    },
    gress( state ){
        return state.gress;
    },
    hotMarker( state ){
        return state.hotMarker;
    }
}

const actions = {
    getDatas( { commit } ){
        Vue.http.jsonp("https://mce.mogucdn.com/jsonp/multiget/3",{
            params:{
                pids: "51833,51836,106930,51822"
            }
        }).then( ( { body: { data } } ) => {
            commit("getData",data);
        })
    },
    getGress( { commit } ){
        Vue.http.jsonp("https://list.mogujie.com/search",{
            params:{
                pid: 9750,
                sort: "pop"
            }
        }).then( ( { body : { result : { wall : { list } } } } ) => {
            commit("getGress",list);
        })
    },
}

const mutations = {
    getData( state, data ){
        state.banner = data[51822].list;
        state.penny = data[106930].list;
        state.hotMarker = data[51836].list;
        state.boon = data[51833].list
        let reg= /^{/;
        let reg2= /}$/;
        for (let index = 0; index < state.boon.length; index++) {
            state.boon[index].viceTitle=state.boon[index].viceTitle.replace(reg,"").replace(reg2,"");
        }
    },
    getGress( state, list ){
        state.gress = list
    }
}

export default {

	state,
	getters,
	actions,
	mutations

}


