
<template>
  	<div class="con">
		<div class="titleDiv">
            <div class="titleList" v-for="(item,index) in rightContentBottom" :key='index' 
                @click="detailsList(item.sortKey,miniWallkey,index)"
                :class = " activeIndex == index ? 'active' : '' ">
                <p>{{ item.title }}</p>
            </div>
        </div>
        <div class="details">
            <div class="detailsList" v-for="(item,index) in rightBottomDetails" :key='index'>
                <router-link :to="{path:'/Details',query:{item}}" >
                    <div class="gradual">
                        已售{{ item.sale }}件
                    </div>
                    <div class="imgDiv">
                        <img :src="item.img">
                    </div>
                    <div class="pContainer">
                        <p class="title">
                            {{ item.title }}
                        </p>
                        <p class="price">
                            ￥{{ item.price }}
                        </p>
                        <p class="starIcon"></p>
                        <p class="count">
                            {{ item.cfav }}
                        </p>
                    </div>
                </router-link>
            </div>
        </div> 
  	</div>
</template>

<script>
	import { mapGetters, mapActions, mapMutations } from "vuex"
	export default {
        data(){
            return {
                activeIndex : 0,
            }
        },
        methods:{
            detailsList(sortKey,miniWallkey,index){
                this.$store.dispatch('getRightContentBottom',{miniWallkey,sortKey});
                this.activeIndex = index;
            }
        },
        computed:{
			...mapGetters([
				"rightContentBottom",
                "rightBottomDetails",
                "leftScroll",
                "miniWallkey"
			])
        },
	};
</script>

<style lang="less" scoped>
	.con{
        .titleDiv{
            margin: 0 0.2rem;
            overflow: hidden;
            display: flex;
            border-top: 0.01rem solid #eee;
            .titleList{
                float: left;
                width: 33.334%;
                height: 0.7rem;
                text-align: center;
                p{
                    height: 50%;
                    margin-top: 12.5%;
                    line-height: 150%;
                    font-size: 0.24rem;
                    color: #666;
                    border-left: 0.01rem solid #eee;
                }
            }
            .active{                                //点击激活时的样式
                p{
                    color: rgb(252, 87, 121);
                    font-weight: bold;
                }
            }
        }

        .details{
            border-top: 0.01rem solid #eee;
            height: 5rem;
            a{
                text-decoration: none;
            }
            .detailsList{
                float: left;
                height: 3.7rem;
                width: 2.25rem;
                margin: 0.1rem 0rem 0  0.1rem;
                border-radius: 0.05rem;
                overflow: hidden;
                .gradual{
                    color: white;
                    position: absolute;
                    font-size: 0.18rem;
                    line-height: 0.35rem;
                    width: 1.4rem;
                    text-align: center;
                    height: 0.35rem;
                    background-image: linear-gradient(to right, rgba(0,0,0,0.6),rgba(0,0,0,0));
                    margin-top: 2.35rem;
                }
                .imgDiv{
                    height: 78%;
                    overflow: hidden;
                    img{
                        width: 100%;
                        height: 106%;
                    }
                }
            }

            .pContainer{
                height: 40%;
                margin: 0.15rem 0.08rem;
                p{
                    font-size: 0.3rem;
                    line-height: 0.3rem;
                    margin-top: 0.05rem;
                }
                .title{
                    font-size: 0.2rem;
                    color: #555;
                    margin-top: -0.1rem;
                    width: 100%;
                    height: 0.3rem;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis; 
                }
                .price{
                    font-weight: bold;
                    float: left;
                    color: rgb(252, 87, 121);
                    font-size: 0.25rem;
                }
                .count{
                    float: right;
                    color: #aaa;
                    font-size: 0.2rem;
                }
                .starIcon{
                    float: right;
                    width: 0.3rem;
                    height: 0.3rem;
                    background: url("data:image/png;base64,") center right no-repeat / 70% 70%;
                }
            }
        }
    }

</style>


