import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

import Home from "./../components/Body/home/home.vue";
import Type from "./../components/Body/type/type.vue";
import Cart from "./../components/Body/cart/cart.vue";
import Me from "./../components/Body/me/me.vue";
import SearchPage from "./../components/SearchPage/SearchPage.vue"
import Details from "./../components/Temp/details/details.vue"

export default new Router({
	mode: "history",	//切换历史模式 去除url中的'#'号
	routes: [
		{
			path: "/",
			component: Home
		},
		{
			path: "/type",
			component: Type
		},
		{
			path: "/cart",
			component: Cart
		},
		{
			path: "/me",
			component: Me
		},
		{
			path: "/SearchPage",
			component: SearchPage
		},
		{
			path: "/Details",
			component: Details
		}
	]
});



