<template>
  	<div class="container">
		<div v-if="rightTitle.length" class="titleImgDiv">
			<img :src="rightTitle[0].image">
		</div>
		<div class="detailItem" v-for="(item,index) in rightContent" :key='index'>
			<div class="imgDiv">
				<img :src="item.image">
			</div>
			<p class="title">{{ item.title }}</p>
		</div>
		<div class="right-content-bottom">
			<right-content-bottom></right-content-bottom>
		</div>
  	</div>
</template>

<script>
	import { mapGetters, mapActions, mapMutations } from "vuex"
	import RightContentBottom from './rightContentBottom/rightContentBottom.vue'
	export default {
        computed:{
			...mapGetters([
				"rightContent",
				"rightTitle"
			])
		},
		components:{
			RightContentBottom
		}
	};
</script>

<style lang="less" scoped>
	.container{
		width: 75%;
		float: right;
		overflow: hidden;
		margin-bottom: 0.9rem;
		.titleImgDiv{
			margin: 0.3rem 0.2rem 0 0.2rem;
			img{
				width: 100%;
			}
		}
		.detailItem{
			margin-top: 0.4rem;
			margin-bottom: 0.4rem;
			float: left;
			width: 33%;
			text-align: center;
			.imgDiv{
				height: 80%;
				img{
					width: 70%;
				}
			}
			.title{
				font-size: 0.21rem;
				color: #555;
			}
		}
		.right-content-bottom{
			width: 100%;
			overflow: hidden;
		}
	}
</style>


