<template>
    <div class="scroll">
        <div class="container">
            <div class="leftItem" v-for="(item,index) in leftScroll" :key='index' 
                @click="getData(item.maitKey,item.miniWallkey,index)"
                :class = " activeIndex == index ? 'active' : '' "
            >
                <p :class = " activeIndex == index ? 'activeP' : '' ">{{ item.title }}</p>
            </div>
        </div>
    </div>
</template>

<script>
	import { mapGetters, mapActions, mapMutations } from "vuex"
	export default {
        data(){
            return {
                activeIndex : -1
            }
        },
        methods:{
            getData(maitKey,miniWallkey,index){
                this.$store.dispatch('getRightContent',maitKey);
                this.$store.dispatch('getRightContentBottom',{miniWallkey});
                this.activeIndex = index;
                // console.log(miniWallkey)
            }
        },
        mounted(){
			this.getData(41888,10062603,0);
		},
        computed:{
			...mapGetters([
				"leftScroll"
			])
        }
	};
</script>

<style lang="less" scoped>
    .scroll{
        width: 25%;
        float: left;
        height: 9.75rem;
        overflow-y: auto;
        background-color: rgb(246, 246, 246);
        position: fixed;
        .container{
            text-align: center;
            color: #666;
            .leftItem{
                height: 0.52rem;
                font-size: 0.25rem;
                padding-top: 15%;
                p{
                    height: 50%;
                }
            }
        }
    }
    .scroll::-webkit-scrollbar{
        display: none;
    }
    .active{
        background-color: white;
    }
    .activeP{
        color: rgb(252, 87, 121);
        border-left: 0.065rem solid rgb(252, 87, 121);
    }
</style>


