
import Vue from 'vue'
const state = {
    commit: [],         //评论数据
    cart: [],           //购物车数据
    total: 0,          //总价
}

const getters = {
    commit( state ){
        return state.commit;
    },
    cart( state ){
        return state.cart;
    },
    total( state ){
        return state.total;
    },
}

const actions = {
    getCommit( { commit }, iid){
        Vue.http.jsonp("https://rate.mogujie.com/h5/rate/jsonp/x6.rate.ratelist/v1",{
            params:{
                itemId : iid,
            }
        }).then( ( { body : { data } } ) => {
            commit("getCommit", data);
        })
    },
    setCart( { commit }, item){
        commit("setCart", item);
    },
    clear( { commit } ){
        commit("clear");
    }
}

const mutations = {
    getCommit( state, rs ){
        state.commit = rs.list;
    },
    setCart( state, rs ){
        let arr = state.cart;
        let flag = false;
        for (let i = 0; i < arr.length; i++) {
            if(arr[i].iid == rs.iid){
                arr[i].count ++;
                console.log(arr[i].count);
                flag = true;
                return;
            }
        }
        console.log(rs.count)
        if(!flag){
            state.cart.push(rs);
        }
        state.total = 0;
        for(var i = 0, len = arr.length; i < len; i++) {
            state.total += arr[i].price*arr[i].count;
        }
        state.total = state.total.toFixed(2);
        console.log(state.cart)
    },
    clear(state){
        state.cart = [];
    }
}

export default {

	state,
	getters,
	actions,
	mutations

}


