<template>
  	<div id="app">
		<!-- 路由 -->
		<keep-alive>
			<router-view></router-view>
		</keep-alive>
		<!-- 页脚 -->
		<foot></foot>
  	</div>
</template>

<script>
	import Home from "./components/Body/home/home.vue";
	import Type from "./components/Body/type/type.vue";
	import Cart from "./components/Body/cart/cart.vue";
	import Me from "./components/Body/me/me.vue";

	import Foot from "./components/Foot/foot.vue"
	import SearchPage from "./components/SearchPage/SearchPage.vue"

	import store from "./store/index"
	export default {
		name: "App",
		store,
		components:{
			Home,
			Type,
			Cart,
			Me,
			Foot,
			SearchPage,
		},
        mounted(){
			this.$store.dispatch('getSearch');
        }
	};
</script>

<style>
	*{
		margin: 0;
		padding: 0;
	}
	body{
		background-color: #f4f4f4;
	}
	a{
		text-decoration: none;
	}
</style>



