<template>
  	<div class="container">
        <div class="searchHead">
            <ul>
                <li @click="$router.history.go(-1)">
                        <p><i class="iconfont icon-xiangzuojiantou"></i></p>
                </li>
                <li>
                    <input type="text" :placeholder="hotSearchFirst">
                </li>
                <li>
                    <p @click="setData()">搜索</p>
                </li>
            </ul>
        </div>
        <div class="history">
            <div>
                <div class="title">
                    <p>
                        <i class="iconfont icon-sousuo"></i>
                        <span>历史搜索</span>
                    </p>
                    <p class="rubbish" @click="clear()">
                        <i class="iconfont icon-lajixiang"></i>
                    </p>
                </div>
                <div class="historyList">
                    <span v-if="!historyDetails.length">暂无历史搜索</span>
                    <div class="historyDetails" v-for="(item,index) in historyDetails" :key="index">
                        {{ item }}
                    </div>
                </div>
            </div>
        </div>
        <div class="hotSearch">
            <div>
                <div class="title">
                    <p>
                    <i class="iconfont icon-star"></i>
                    <span>热门搜索</span>
                </p>
                </div>
                <div class="hotSearchList">
                    <div class="hotSearchDetails" v-for="(v,i) in hotSearch" :key="i" :style="{color:v.color}">
                        {{ v.query }}
                    </div>
                </div>
            </div>
        </div>
  	</div>
</template>

<script>
	import { mapGetters, mapActions, mapMutations } from "vuex"
	export default {
        data(){
            return {
                historyDetails : []
            }
        },
        methods:{
            setData(){
                let input = document.querySelector("input");
                if(!input.value){
                    this.historyDetails.push(input.placeholder)
                }else{
                    this.historyDetails.push(input.value)
                }
                this.historyDetails = Array.from(new Set(this.historyDetails));     //利用Array.from将Set结构转换成数组
            },
            clear(){
                this.historyDetails = []
            }
        },
		computed:{
			...mapGetters([
                "hotSearch",
                "hotSearchFirst"
			])
        },
	};
</script>

<style lang="less" scoped>
    .container{
        width: 100%;
        background-color: white;
        height: 11.35rem;
        position: absolute;
        z-index: 9999;
        font-size: 0.25rem;
        color: #999;
        .searchHead{
            height: 0.77rem;
            border-bottom: 0.01rem solid #eee;
            color: #777;
            text-align: center;
            line-height: 0.77rem;
            li{
                float: left;
                font-size: 0.25rem;
                list-style: none;
                margin-left: 0.1rem;
                a{
                    text-decoration: none;
                    color: #777;
                }
                .iconfont{
                    font-size: 0.38rem;
                }
                input{
                    width: 5rem;
                    height: 0.4rem;
                    border: 0.01rem solid #aaa;
                    border-radius: 0.05rem;
                    text-indent: 0.2rem;
                }
                input:focus{
                    outline: none;
                    border: 0.01rem solid rgb(238, 125, 125);
                }
                input::placeholder{
                    color: #aaa;
                }
            }
        }
        .iconfont{
            font-size: 0.25rem;
        }
        .history{
            width: 100%;
            font-size: 0.2rem;
            text-align: center;
            border-bottom: 0.01rem solid #eef;
            overflow: hidden;
            .title{
                overflow: hidden;
                p{
                    float: left;
                    margin-left: 0.2rem;
                    height: 0.5rem;
                    line-height: 0.5rem;
                }
                .rubbish{
                    float: right;
                    margin-right: 0.2rem;
                }
            }
            .historyList{
                float: left;
                margin: 0.2rem 0.5rem 0.3rem;
                overflow: hidden;
                .historyDetails{
                    float: left;
                    margin-left: 0.2rem;
                    margin-top: 0.2rem;
                    border: 0.01rem solid #eee;
                    padding: 0.08rem 0.17rem;
                    border-radius: 0.05rem;
                    color: #444;
                }
            }
        }
        .hotSearch{
            width: 100%;
            font-size: 0.2rem;
            text-align: center;
            overflow: hidden;
            .title{
                overflow: hidden;
                p{
                    float: left;
                    margin-left: 0.2rem;
                    height: 0.5rem;
                    line-height: 0.5rem;
                }
            }
            .hotSearchList{
                float: left;
                margin: 0.2rem 0.5rem;
                overflow: hidden;
                .hotSearchDetails{
                    float: left;
                    margin-left: 0.2rem;
                    margin-top: 0.2rem;
                    border: 0.01rem solid #eee;
                    padding: 0.08rem 0.17rem;
                    border-radius: 0.05rem;
                    color: #444;
                }
            }
        }
    }
</style>


